.nav-links {
    display: flex;
    justify-content: space-around;
    width: 570px;
    font-family: $monser;
    padding-top: 20px;

    a {
        color: white;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 700;

        &:hover {
            color: #D4AF37;
        }
    }
}

.blend {
    h2 {
        font-size: 22px;
        mix-blend-mode: difference;
        font-family: $roboto;
        writing-mode: vertical-lr;
        transform: rotate(180deg);
    }
}

.intro {
    overflow: hidden;
    white-space: nowrap;
    font-size: 1.4em;
    font-weight: bold;
    letter-spacing: 1.2em;
    mix-blend-mode: difference;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    // animation: animIntro 3.5s steps(23, end);
    font-family: $poppins;
    // animBar 1s step-end infinite;

}

.header {

    background: #1d2025;


    &_container {
        // position: fixed;
        max-width: 1170px;
        display: flex;
        justify-content: space-between;
        margin: auto;

    }

    &__logo {
        font-family: $poppins;
        font-size: 30px;
        font-weight: lighter;
        color: #1d2025;
        background-color: #FFBA00;
        max-width: 100px;
        width: 100%;
        text-align: center;
        padding-top: 20px;

        &-hr {
            width: 150px;
            background-color: white;
            border: none;
            height: 1px;
            margin-top: 30px;

        }
    }

    &_logos {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        max-width: 500px;
        width: 100%;
        color: rgb(204, 201, 201);
        position: absolute;
        top: 20%;
        left: 2%;

        hr {
            transform: rotate(90deg);
            width: 20px;
            margin: 20px 5px;
            color: rgb(204, 201, 201);
        }

        &_item {
            display: flex;
            font-size: 12px;
            justify-content: center;
            align-items: center;
            margin: 10px 2px;
            border: white 1px solid;
            border-radius: 50%;
            width: 30px;
            height: 30px;
            padding: 2px 5px;
            cursor: pointer;

            &:hover {
                color: #FFBA00;
                border-color: #FFBA00;
            }
        }
    }

    &__info {
        display: flex;
        flex-direction: row;
        color: white;
        max-width: 500px;
        width: 100%;
        line-height: 2em;
        align-self: flex-start;
        font-family: $poppins;
        margin: 100px 0 100px;

        &-title {
            font-size: 60px;
            color: rgb(201, 196, 196);
            text-transform: uppercase;
            font-weight: bold;
            margin: 80px 20px 0;
            width: 100%;
        }

        &-word {
            text-transform: uppercase;
            color: #FFBA00;
            font-size: 80px;
            font-weight: bold;
            mix-blend-mode: color-dodge;
        }
    }

    &_button {
        position: relative;
        color: #FFBA00;
        width: 180px;
        height: 50px;
        background-color: transparent;
        border: none;
        border-bottom: 2px solid #FFBA00;
        border-right: 2px solid #FFBA00;
        font-size: 18px;
        font-family: $poppins;
        cursor: pointer;
        transition: ease-in-out 1s;
        margin-bottom: 20px;

        // &::after,
        // &::before {
        //     content: '';
        //     position: absolute;
        //     width: 25%;
        //     height: 25%;
        //     border: 2px solid #FFBA00;
        //     transition: all .8s ease;
        // }

        // &::after {
        //     bottom: 0;
        //     right: 0;
        //     border-top-color: transparent;
        //     border-left-color: transparent;
        // }

        // &::before {
        //     top: 0;
        //     left:  0;
        //     border-bottom-color: transparent;
        //     border-right-color: transparent;
        // }

        // &:hover::after,
        // &:hover::before {
        //     wid
        // }

        &:hover {
            border: 2px solid #FFBA00;
            color: rgb(221, 186, 27);
            box-shadow: inset 3px 10px 20px #FFBA00;
        }

        &-arrow {
            font-size: 30px;
            margin: auto;

        }
    }
}

.period {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    top: 0;
    right: 0%;
    font-family: $monser;
    font-weight: bold;
    font-size: 200px;
    // transform: skew(0deg, -15deg);
    color: transparent;
    opacity: .3;
    z-index: -4;
    text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.5);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    background-color: gray;
    letter-spacing: .4em;

}


