.works {
    background-color: #1d2025;
    margin: auto;
    overflow: hidden;
    position: relative;

    .square {
        position: absolute;
        background: linear-gradient(#FFBA00, #1d2025);
        max-width: 500px;
        width: 100%;
        height: 100%;
        max-height: 500px;
        top: 10%;
        left: 30%;

    }

    .background-word {
        color: #2d2b31;
        font-size: 200px;
        top: 0;
        z-index: 1;
        font-family: $poppins;
        font-weight: bold;
        position: absolute;
    }

    &__title {
        position: relative;
        z-index: 100;
        font-family: $poppins;
        font-weight: bold;
        font-size: 92px;
        text-align: center;
        color: white;
        letter-spacing: 1.2rem;
        max-width: 100px;
        width: 100%;
        padding: 0 10px;
    }

    .swiper-container {
        padding: 20px;
    }

    .swiper-slide {
        text-align: center;
        font-size: 18px;


        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
    }

    img {
        max-width: 780px;
        width: 100%;
    }

    .swiper-pagination-bullet {
        background-color: #ffba00;
    }

    .swiper-button-next,
    .swiper-button-prev {
        color: #ffba00;

        &:hover {
            transform: scale(1.2);
        }
    }

    .gallery-thumbs {
        // background: linear-gradient(#1d2025, #FFBA00);

        img {
            max-width: 300px;
            width: 100%;
        }
    }
}

