* {
    padding: 0;
    margin: 0;
}

html {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
    box-sizing: inherit;
}

:focus {
    outline: none;
}

button {
    display: inline-block;
    padding: 0;
    margin: 0;
    line-height: 1;
    text-align: center;
    border: none;
    background: none;
    cursor: pointer;
}

