.sl {
    position: relative;
    height: 92vh;
    background-color: #1d2025;

    &__title {
        padding: 5px;
        width: 100%;
        position: absolute;
        top: -10%;
        font-family: $poppins;
        font-size: 200px;
        font-weight: bold;
        color: #2b2d31;
    }
}

article {
    // background-color: #FFBA00;
    max-width: 700px;
    width: 100%;
    height: 550px;
    padding: 60px 0;
    border-bottom-left-radius: 50%;
    position: absolute;
    right: 0;


}

.slider {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.slick {
    position: relative;
    overflow: hidden;

    .item {
        float: left;
        text-align: center;
        max-width: 700px;
        height: 550px;


        img {
            display: block;
            margin: 0 auto;
            max-width: 650px;
            width: 100%;
        }
    }
}

.slick-no-slide {
    .slick-track {
        width: 100% !important;
        text-align: center;
        transform: inherit !important;
    }

    .slick-slide {
        float: none;
        display: inline-block;
    }
}

.slick-list {
    position: relative;
    overflow: hidden;
}

.slick-dots {
    text-align: center;

    li {
        display: inline-block;

        button {
            width: 10px;
            height: 10px;
            margin: 0 5px;
            font-size: 0;
            background-color: grey;
            border-radius: 50%;
            border: none;
        }
    }

    .slick-active {
        button {
            background-color: black;
        }
    }
}

.slick-prev {
    left: 10px;
    background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.slick-next {
    right: 10px;
    background: url("data:image/png;base64,") no-repeat 50% 50%;
}

.slick-arrow {
    position: absolute;
    top: 48%;
    z-index: 1;
    width: 15px;
    height: 30px;
    font-size: 0;
    background-size: 100% 100%;
}

.slick-disabled {}

