        font-weight: 400;

        &-desc {
            color: white;
        }
    }

    &-section {
        margin: 40px 0;
        padding: 0px 10px;
        background-color: #2b2d31;
        // border-top: 2px solid #FFBA00;
        // border-left: 2px solid #FFBA00;

    }

    &-form {
        max-width: 600px;
        width: 100%;
        margin: auto;


        &-text {
            display: block;
            background-color: #111;
            width: 100%;
            box-sizing: border-box;
            margin: 16px 0;
            border: 0;
            padding: 20px 40px;
            outline: none;
            color: #ddd;
            transition: 0.5s;

            &:focus {
                box-shadow: 0 0 10px 4px #34495e;
            }
        }

        &-btn {
            position: relative;
            color: #FFBA00;
            width: 180px;
            height: 50px;
            background-color: transparent;
            border: none;
            border-bottom: 2px solid #FFBA00;
            border-right: 2px solid #FFBA00;
            font-size: 18px;
            font-family: $poppins;
            cursor: pointer;
            transition: ease-in-out 1s;
            margin-bottom: 20px;



            &:hover {
                border: 2px solid #FFBA00;
                color: rgb(221, 186, 27);
                box-shadow: inset 3px 10px 20px #FFBA00;
            }
        }

        textarea {
            resize: none;
            height: 220px;
        }
    }

    @keyframes animIntro {
        0% {
            height: 0;
        }

        100% {
            height: 265px;
        }
    }

    @-webkit-keyframes fadeInRight {
        from {
            opacity: 0;
            -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
        }

        to {
            opacity: 1;
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
    }

    @keyframes fadeInRight {
        from {
            opacity: 0;
            -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
        }

        to {
            opacity: 1;
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
    }

    @keyframes rotation {
        from {
            -webkit-transform: rotate(0deg);
        }

        to {
            -webkit-transform: rotate(359deg);
        }
    }
}